

.komanda__wrapper{
    position: relative;
    z-index: 20;
    // backdrop-filter: blur(10px);
    background-color: #f5f0ec;
    padding-top: 100px;
}
.komanda__container{
    position: relative;
    z-index: 20;
    width: 100%;
    height: 100%;
    margin: 0 0 0 auto;
    overflow: hidden;
    .komanda__text{
        color: #f5f0ec;
        font-family: 'Bodoni Moda';
        text-transform: uppercase;
        font-size: 150px;
        font-weight: 200;
        
        .komanda__text__container{
            display: flex;
            align-items: center;
            height: 170px;
            overflow: hidden;
            @media (max-width:700px){
                font-size: 100px;
                height: 100px;
            }
            h4{
                color: #000;
            }
            span{
                color: transparent;
                -webkit-text-stroke: 2px #dbd8d6;
            }
            img{
                width: 120px;
                height: 140px;
                @media (max-width:700px){
                    height: 80px;
                    width: 60px;
                }
            }
        }
        
    }
}
