
.absolute{
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    background-color: black;
    z-index: 2;
}
.loader__wrapper{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    transition: "all 0.25s ease-in-out";
    background-color: #191919;
    color: #dbd8d6;

    h2{
        font-size: 1.5vw;
        text-transform: uppercase;
        font-family: 'Bai Jamjuree', sans-serif;
        font-weight: 600;
    }
    h4{
        font-size: 1.5vw;
        text-transform: uppercase;
        margin-top: 10px;
        font-family: 'Bodoni Moda', serif;
        font-style: italic;
    }
}
.main__container{
    width: 100%;
    height: 100%;
    .scroll__container{
        // position: relative;
        // z-index: 20;
        .scrollGorizont__container{
            color: #191919;
        }
    }
}

