.work__wrapper{
    position: relative;
    z-index: 20;
    width: 100%;
    height: 1000px;
    overflow: hidden;
    @media (max-width:1250px){
        height: 682px;
    }
    @media (max-width:870px){
        height: 440px;
    }
    
    h2{
        font-size: 290px;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        color: #5983fc;
        @media (max-width:1250px){
                font-size: 200px;
        }
        @media (max-width:870px){
            font-size: 130px;
        }
        @media (max-width:573px){
            font-size: 90px;
            top: 62.9%;
        }
        @media (max-width:390px){
            font-size: 70px;
            top: 70%;
        }
    }
    .work__top{
        position: absolute;
        top: 0%;
        width: 100%;
        height: 50%;
        background-color: #f5f0ec;
        overflow: hidden;
        .work__top_item{
            bottom: 70%!important;
        }
    }
    .work__center{
        width: 100%;
        height: 100%;
        background-color: #000;
        color: #5983fc;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        h3{
            font-size: 8vw;
            text-align: center;
            text-transform: uppercase;
        }
        h4{
            font-size: 5vw;
            letter-spacing: 10px;
            font-weight: 300;
        }
        
    }
    .work__bottom{
        position: absolute;
        bottom: 0%;
        width: 100%;
        height: 50%;
        background-color: #f5f0ec;
        overflow: hidden;
        .work__bottom_item{
            top: -6.5%!important;
            @media (max-width:1250px){
                top: 5%;
            }
            @media (max-width:390px){
                top: 20%;
            }
        }
    }
}