
    
.portfolio_bg{
    position: relative;
    z-index: 20;
    height: 100%;
    width: 100%;
    margin-bottom: 10%;
    transition: initial;
    .portfolio_bg_img{
        position: fixed;
        width: 100%;
        height: 100%;
        object-fit: cover;
        z-index: -1;
        }
.portfolio_bg_wrapper{
    h2{
        margin: 0 auto;
        padding: 0px 0 500px 0;
        text-align: center;
        font-size: 14vw;
        color: #f5f0ec;
        font-weight: 700;
        text-rendering: geometricPrecision;
        letter-spacing: 8px;
        padding: 400px 0;
        @media (max-width:840px){
          // padding: 300px 0;
        }
    }
    .portfolio_bg_button{
        z-index: 12;
        position: relative;
        padding: 10px 20px;
        font-size: 2vw;
        background-color: #fff;
        color: #000;
        margin: 30px auto 0;
        display: flex;
        justify-content: center;
        align-items: center;
        text-transform: uppercase;
        border-radius: 10px;
        transition: .3s ease-in-out;
        @media (max-width:800px){
          font-size: 20px;
        }
        a{
          color: #000;
          display: flex;
          align-items: center;
          img{
            width: 50px;
            height: 50px;
            margin-left: 10px;
          }
        }
        // &:hover{
        //   transform: translate(0, 4px);
        //   box-shadow: 0 0 0 #000;
        //   border: 1px solid #000;
        // }
    }
    .portfolio_bg_grid{
      width: 100%;
      height: 100%;
      margin-top: -200px;
      // display: flex;
      display: grid;
      // flex-direction: column;
      .portfolio_bg_grid_1{
        display: flex;
        justify-content: flex-end;
        img{
          object-fit: cover;
          width: 70%;
          height: 1000px;
          @media (max-width:1100px){
            height: 800px;
            width: 60%;
          }
          @media (max-width:800px){
            width: 80%;
          }
        }
      }
      .portfolio_bg_grid_2{
        width: 100%;
        height: 100%;
        margin-top: -5%;
        z-index: -1;
        img{
          width: 90%;
          object-fit: cover;
        }
      }
      .portfolio_bg_grid_3{
        width: 100%;
        height: 100%;
        margin-top: 5%;        
        display: flex;
        justify-content: center;
        img{
          width: 70%;
          object-fit: cover;
          height: 1000px;
          @media (max-width:1100px){
            height: 800px;
            width: 60%;
          }
          @media (max-width:800px){
            width: 80%;
          }
          @media (max-width:510px){
            width: 90%;
          }
        }
      }
      .portfolio_bg_grid_4{
        width: 100%;
        height: 100%;
        margin-top: -5%; 
        display: flex;
        justify-content: flex-end;
        z-index: -1;
        img{
          width: 80%;
          object-fit: cover;
        }
      }
      .portfolio_bg_grid_5{
        width: 100%;
        height: 100%;
        margin-top: 5%; 
        display: flex;
        justify-content: flex-end;
        img{
          width: 60%;
          height: 900px;
          object-fit: cover;
          @media (max-width:510px){
            width: 85%;
          }
        }
      }
      .portfolio_bg_grid_6{
        margin-top: -7%; 
        margin-left: 2%;
        z-index: -1;
        img{
          width: 80%;
          object-fit: cover;

        }
      }
      .portfolio_bg_grid_7{
        margin-top: 5%;
        display: flex;
        justify-content: flex-end;
        img{
          width: 80%;
          object-fit: cover;
        }
      }
      .portfolio_bg_grid_8{
        margin-top: -5%;
        margin-left: 2%;
        z-index: -1;
        
        img{
          width: 50%;
          height: 1000px;
          object-fit: cover;  
          @media (max-width:1100px){
            height: 800px;
            width: 60%;
          }
          @media (max-width:800px){
            width: 80%;
          }
          @media (max-width:510px){
            width: 90%;
          }
        }
      }
    }
  }
}
