.about__wrapper{
    position: relative;
    z-index: 20;
    background-color: #f5f0ec;
    padding-top: 100px;
    .about__container{
    width: 100vw;
    height: 100vh;
    @media (max-width:717px){
        width: 100%;
        height: 100%;
    }
    .about__mask, .about__body{
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        color: #000;
        font-size: 64px;
        line-height: 66px;
        padding-top: 10%;
        text-align: center;
        cursor: default;
        @media (max-width:895px){
            font-size: 50px;
            padding-top: 0%;
        }
        @media (max-width:717px){
            font-size: 40px;
            text-align: left;
        }
        @media (max-width:500px){
            font-size: 30px;
        }
        p{
            width: 1000px;
            padding:40px;
            @media (max-width:895px){
                width: 100%;
                padding: 5px;
            }
          }
          span{
            color: #4bb6f4;
          }
    }
    .about__mask{
        mask-image: url('../../../public/1.svg');
        mask-repeat: no-repeat;
        mask-size: 40px;
        background: #4bb6f4;
        position: absolute;
        color: #fff;
        @media (max-width:895px){
            display: none;
        }
      }
    }
}