


// класс применяется ко всему тексту в горизонтальном скроле
.scrollGorizont__text{
  width: 500px;
  margin-top: 5vw;
  color: #fff;
  font-family: "BeatriceBold";
  @media (max-width:1000px){
    width: 100%;
  }
  h3{
      text-transform: uppercase;
      font-size: 4vw;
      text-decoration-line: overline;
      @media (max-width:510px){
        font-size: 30px;
      }
  }
  h4{
      margin-top: 100px;
      font-size: 40px;
      backdrop-filter: blur(3px);
  }
  ul{
      margin: 10px 0 0 30px;
      font-size: 30px;
      backdrop-filter: blur(3px);
      li{
          list-style-type: disc;	
      }
  }
  p{
      margin-top: 20px;
      font-size: 22px;
      backdrop-filter: blur(2px);
      @media (max-width:1000px){
        width: 80%;
      }
      @media (max-width:410px){
        font-size: 18px;
      }
  }
}

