$ruler: 16px;
$color-red: #AE1100;
$color-bg: #EBECF0;
$color-shadow: #BABECC;
$color-white: #FFF;


.footer{
    position: relative;
    z-index: 25;
    background-color: $color-bg;
    .footer__wrapper{
        display: flex;
        justify-content: space-between;
        @media (max-width:1270px){
          flex-direction: column;
          align-items: center;
        }
        .footer__container{
            background-color:$color-bg;
            display: flex;
            flex-direction: column;
            flex-wrap: nowrap;
            font-family: 'Bai Jamjuree';
            @media (max-width:1270px){
              width: 100%;
            }
        .footer__logo{
          width: 700px;
          font-size: 10vw;
          color: blue;
          font-family: "BeatriceBold";
          text-transform: uppercase;
          h5{
            color: $color-bg;
          }
          @media (max-width:1270px){
            width: 100%;
          }
        }
        .footer__contact{
          @media (max-width:1270px){
            display: flex;
            justify-content: space-around;
            align-items: center;
            margin: 50px 0;
            padding-left: 10px;
          }
          @media (max-width:970px){
            flex-direction: column;
            margin: 0 auto;
          }
        }
        form {
            font-family: 'Bai Jamjuree';
            letter-spacing: 2px;
            font-size: $ruler;
            width: 400px;
            @media (max-width:600px){
              width: 90%;
              display: flex;
              flex-direction: column;
              align-items: center;
              justify-content: center;
            }
          }
          .segment {
            padding: $ruler*2 0;
            text-align: center;
            color: $color-red;
            color:#61677C;
            text-shadow: 1px 1px 1px $color-white;
            font-size: 28px;
          }
          
          button, input {
            border: 0;
            outline: 0;
            font-size: $ruler;
            border-radius: $ruler*20;
            padding: $ruler;
            background-color:$color-bg;
            text-shadow: 1px 1px 0 $color-white;
          }
          
          label {
            display: block;
            margin-bottom: $ruler*1.5;
            width: 100%;
          }
          
          input {
            margin-right: $ruler/2;
            box-shadow:  inset 2px 2px 5px $color-shadow, inset -5px -5px 10px $color-white;
            width: 100%;
            box-sizing: border-box;
            transition: all 0.2s ease-in-out;
            appearance: none;
            -webkit-appearance: none;
          
            &:focus {
              box-shadow:  inset 1px 1px 2px $color-shadow, inset -1px -1px 2px $color-white;
            }
          }
          
          button {
            color:#61677C;
            font-weight: bold;
            box-shadow: -5px -5px 20px $color-white,  5px 5px 20px $color-shadow;
            transition: all 0.2s ease-in-out;
            cursor: pointer;
            font-weight: 600;
            
            &:hover {
              box-shadow: -2px -2px 5px $color-white, 2px 2px 5px $color-shadow;
            }
            
            &:active {
              box-shadow: inset 1px 1px 2px $color-shadow, inset -1px -1px 2px $color-white;
            }
            
            &.red {
              display: block;
              width: 100%;
            }
          }
        }
        .footer__connect{
          margin: 150px 0 5% 5%;
          font-weight: 100;
          @media (max-width:1270px){
            margin: auto 0 0 5%;
          }
          @media (max-width:970px){
            margin: 0 0 0 0%;
          }
          ul{
            display: flex;
            width: 650px;
            @media (max-width:970px){
              width: 80%;
              margin: 50px auto;
            }
            @media (max-width:600px){
              width: 100%;
            }
            
            .footer__connect__right{
              margin-left: 80px;
              @media (max-width:510px){
                margin-left: 20px;
              }
            }
            .footer__connect__item{
              font-size: 20px;
              width: 270px;
              @media (max-width:600px){
                width: 100%;
              }
              img{
                width: 40px;
                height: 40px;
                margin-right: 10px;
                color: #fff;
              }
              a{
                color: #000;
                position: relative;
                z-index: 20;
                width: 100%;
                &::after{
                  content: "";
                  position: absolute;
                  left: 0;
                  bottom: 50%;
                  height: 0%;
                  width: 270px;
                  background-color: #8894b0;
                  transition:.3s ease-in-out;
                  z-index: 25;
                }
                &::before{
                  content: "";
                  position: absolute;
                  left: 0;
                  top: 50%;
                  height: 0%;
                  width: 270px;
                  background-color: #8894b0;
                  transition:.3s ease-in-out;
                  z-index: 25;
                }
                span{
                  position: absolute;
                  left: 10px;
                  top: 0px;
                  height: 100%;
                  width: 270px;
                  color: #FFF;
                  opacity: 0;
                  transition: .3s ease-in-out;    
                }
              }:hover span{
                z-index: 30;
                opacity: 1;
                @media (max-width:600px){
                  display: none;
                }
              }
              :hover::after{
                height: 50%;
                @media (max-width:600px){
                  display: none;
                }
              }
              :hover::before{
                height: 50%;
                @media (max-width:600px){
                  display: none;
                }
              }
            }
          }
          
        }
        .footer__images{
            width: 50%;
            @media (max-width:1270px){
              width: 80%;
            }
            @media (max-width:600px){
              width: 100%;
            }
        }
    }
}
