// Шрифты
// два шрифта Bai+Jamjuree:200;400;600;, Syncopate:400;700; Bodoni+Moda:400;600
@import url('https://fonts.googleapis.com/css2?family=Bai+Jamjuree:wght@200;400;600&family=Bodoni+Moda:ital,opsz,wght@0,6..96,400;0,6..96,600;1,6..96,600&family=Syncopate:wght@400;700&display=swap');
// font-family: 'Bai Jamjuree', sans-serif;
// font-family: 'Syncopate', sans-serif;
// font-family: 'Bodoni Moda', serif;



@font-face {
	font-family: "BeatriceBold";
	src: local("Beatrice Display Bold"),
	 url("./fonts/Beatrice Display/Beatrice Display Bold.otf") format("opentype");
	font-weight: 700;
}
.font-BeatriceBold{
	font-family: "BeatriceBold";
}
$bg_color: #f5f0ec;
body{
	height: 100vh;
	width: 100%;
	margin: 0 auto;
	// cursor: none;
	background-color: $bg_color;
	scroll-behavior: smooth;
	will-change:auto;

}
.container{
	overflow: hidden;
}





/* Reset and base styles  */
* {
	padding: 0px;
	margin: 0px;
	border: none;
}

*,
*::before,
*::after {
	box-sizing: border-box;
}

/* Links */

a, a:link, a:visited  {
    text-decoration: none;
}

a:hover  {
    text-decoration: none;
}

/* Common */

aside, nav, footer, header, section, main {
	display: block;
}

h1, h2, h3, h4, h5, h6, p {
    font-size: inherit;
	font-weight: inherit;
}

ul, ul li {
	list-style: none;
}

img {
	vertical-align: top;
}

img, svg {
	max-width: 100%;
	height: auto;
}

address {
  font-style: normal;
}

/* Form */

input, textarea, button, select {
	font-family: inherit;
    font-size: inherit;
    color: inherit;
    background-color: transparent;
}

input::-ms-clear {
	display: none;
}

button, input[type="submit"] {
    display: inline-block;
    box-shadow: none;
    background-color: transparent;
    background: none;
    cursor: pointer;
}

input:focus, input:active,
button:focus, button:active {
    outline: none;
}

button::-moz-focus-inner {
	padding: 0;
	border: 0;
}

label {
	cursor: pointer;
}

legend {
	display: block;
}
