.storage__title{
    position: absolute;
    left: 50%;
    top: 0%;
    transform: translate(-50%, -50%);
    font-size: 3vw;
    width: 100%;
    height: 100%;
    z-index: 20;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    transition: .3s ease-in-out;
    color: transparent;
    text-transform: uppercase;
    font-family: "BeatriceBold";
    letter-spacing: 3px;
    span{
        margin-top: 20px;
        font-size: 2vw;
        letter-spacing: 5px;
        text-transform: uppercase;
        font-family: "BeatriceBold";
    }
}

.picture__wrapper{
    background-color: #EBECF0;
.picture__container{
    .storage{
        height: 100%;
        width: 100%;
        z-index: 19;
        margin-bottom: 100px;
        h4{
            // padding-top: 120px;

            font-size: 10vw;
            font-family: "BeatriceBold";
            color: #000;
            color: #4bb6f4;
            text-align: center;
            text-transform: uppercase;
            @media (max-width:450px){
                font-size: 40px;
            }
        }
        .marquee__container{
            // margin: 100px 0;
            height: 400px;
            display: flex;
            border-top: 1px solid #000000;
            border-bottom: 1px solid #000;
            padding: 50px 0;
            img{
                width: 100%;
                height: 100%;
                object-fit: cover;
                padding-right: 20px;
            }
        }
        // первый грид
        .storage__item_1{
            display: grid; 
            grid-template-columns: 1fr 1fr 2fr 1fr;
            gap: 40px;
            height: 100%;
            width: 100%;
            z-index: 20;
            @media (max-width:800px){
                display: flex;
                flex-direction: column;
                gap: 20px;
            }
            img{
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
            video{
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
             .storage__img1_1 {
                position: relative;
                grid-row-start: 1;
                grid-column-start: 1;
                grid-row-end: 3;
                grid-column-end: 3;
                overflow: hidden;
                
             }:hover h3{
                background-color: rgba($color: #000000, $alpha: .5);
                color: #fff;
                top: 50%;
            }
             .storage__img1_2 {
                position: relative;
                grid-row-start: 1;
                grid-column-start: 3;
                grid-row-end: 2;
                grid-column-end: 4;
                overflow: hidden;
                
             }
             .storage__img1_3 {
                position: relative;
                background-color: #B67FFB; 
                grid-row-start: 1;
                grid-column-start: 4;
                grid-row-end: 2;
                grid-column-end: 5;
                overflow: hidden;
             }
            .storage__img1_4 {
                position: relative;
                background-color: #9DA9DC; 
                grid-row-start: 2;
                grid-column-start: 3;
                grid-row-end: 4;
                grid-column-end: 5;
                overflow: hidden;
            }
            .storage__img1_5 {
                position: relative;
                background-color: #C657B9; 
                grid-row-start: 3;
                grid-column-start: 2;
                grid-row-end: 4;
                grid-column-end: 3;
                overflow: hidden;
            }
            .storage__img1_6 {
                position: relative;
                overflow: hidden;
                background-color: #7F98DB; 
                grid-row-start: 3;
                grid-column-start: 1;
             
                grid-row-end: 4;
                grid-column-end: 2;
            }
            }
            // второй грид
        .storage__item_2{
            margin: 20px 0;
            display: grid;  
            grid-template-columns: 1fr 1fr 1fr 1fr;
            grid-template-rows: 300px;
            gap: 20px;
            width: 100%;
            height: 100%;
            z-index: 20;
            @media (max-width:700px){
                grid-template-columns: 1fr 1fr;
            }
            @media (max-width:420px){
                grid-template-columns: 1fr;
            }
            img{
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
            .storage__img2_1{
                position: relative;
                overflow: hidden;
            }
            .storage__img2_2{
                position: relative;
                overflow: hidden;
            }
            .storage__img2_3{
                position: relative;
                overflow: hidden;
            }
            .storage__img2_4{
                position: relative;
                overflow: hidden;
            }
            :hover h3{
                background-color: rgba($color: #000000, $alpha: .5);
                color: #fff;
                top: 50%;
            }
        }
        // трутий грид
        .storage__item_3{
            display: grid;  
            grid-template-columns: 1fr 1fr 1fr 1fr;
            grid-template-rows: 300px;
            gap: 20px;
            @media (max-width:700px){
                grid-template-columns: 1fr 1fr;
            }
            @media (max-width:420px){
                grid-template-columns: 1fr;
            }
            img{
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
            .storage__img3_1{
                position: relative;
                overflow: hidden;
            }
            .storage__img3_2{
                position: relative;
                overflow: hidden;
            }
            .storage__img3_3{
                position: relative;
                overflow: hidden;
            }
            .storage__img3_4{
                position: relative;
                overflow: hidden;
            }
            :hover h3{
                background-color: rgba($color: #000000, $alpha: .5);
                color: #fff;
                top: 50%;
            }
        }
        .storage__item_4{
            margin-top: 20px;
            display: grid; 
            grid-template-columns: 1fr 250px 250px 1fr;
            gap: 20px;
            @media (max-width:800px){
                display: flex;
                flex-direction: column;
                gap: 20px;
            }
            img{
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
            
         .storage__img4_1 {
            background-color: #E885D9; 
            grid-row-start: 1;
            grid-column-start: 1;
            grid-row-end: 2;
            grid-column-end: 2;
            position: relative;
            overflow: hidden;
         }
         .storage__img4_2 {
         
            background-color: #EDA956; 
            grid-row-start: 2;
            grid-column-start: 1;
            position: relative;
            overflow: hidden;
            grid-row-end: 3;
            grid-column-end: 2;
            
         }
         .storage__img4_3 {
         
            background-color: #77EBBB; 
            grid-row-start: 1;
            grid-column-start: 2;
            position: relative;
            overflow: hidden;
            grid-row-end: 3;
            grid-column-end: 4;
            
            
         }
         .storage__img4_4 {
         
            background-color: #CBA86B; 
            grid-row-start: 1;
            grid-column-start: 4;
            position: relative;
            overflow: hidden;
            grid-row-end: 2;
            grid-column-end: 5;
            
         }
         .storage__img4_5{
         
            background-color: #7AB95D; 
            grid-row-start: 2;
            grid-column-start: 4;
            position: relative;
            overflow: hidden;
            grid-row-end: 3;
            grid-column-end: 5;
            
            }
            :hover h3{
                background-color: rgba($color: #000000, $alpha: .5);
                color: #fff;
                top: 50%;
            }
        }
        
    }

    }
}